import mitt, { type Emitter } from "mitt"

/**
 * 
 * 
 */
export const EVENT_PAGE_BACK_FROM_CACHE = "pageBackFromCache"

export type Events = {
    pageEvent: string
    lensFilterChange: {
        selectedTags: string[]
    }
}


var emitter: Emitter<Events> | null = null

export function getEventEmitter() {
    if (emitter == null) {
        emitter = mitt<Events>()
    }
    return emitter
}