import "./AboutMeDialog.scss"
import { ABOUT_DIALOG_WRAPPER_ID, BaseDialog, showDialog } from "./BaseDialog"
import type { BaseDialogOpenProps } from "./BaseDialog"
import { setupListItemRipple } from "../list"
import React, { useEffect, useMemo } from "react"
import { Tag } from "../react/Tag"

function AboutMeDialog(props: BaseDialogOpenProps) {

    const kunQvLink = useMemo(() => {
        return window.location.origin + "/post/original/2019/05/18/.html"
    }, [])

    return (
        <BaseDialog openCount={props.openCount}>
            <div className="center-inline-items">
                <picture>
                    <source srcSet="https://apqx.oss-cn-hangzhou.aliyuncs.com/blog/site/me_emoji.webp"
                        type="image/webp" />
                    <img className="circle-avatar inline-for-center" alt="avatar"
                        src="https://apqx.oss-cn-hangzhou.aliyuncs.com/blog/site/me_emoji.png" />
                </picture>
                <p className="about-me-name"></p>
                <section className="btn-tag-container about-me-tag-wrapper">
                    <Tag text="C++" />
                    <Tag text="Java" />
                    <Tag text="Kotlin" />
                    <Tag text="Android" />
                    {/* <Tag text="Git" /> */}
                </section>
                <p className="about-me-description"><a
                    href={kunQvLink} tabIndex={-1}></a> Fans<a href="mailto:safari@mudan.me" tabIndex={-1}></a><a
                        href="https://apqx.oss-cn-hangzhou.aliyuncs.com/blog/site/wechat.jpg" target="_blank" tabIndex={-1}></a></p>
                <ul className="mdc-deprecated-list mdc-deprecated-list--one-line dialog-link-list" id="about-me-dialog_link_list">
                    <LinkItem link="https://github.com/apqx" title="GitHub" />
                    <LinkItem link="https://www.youtube.com/channel/UCF3Qv9tpULGL-CabxSEaCaQ" title="YouTube" />
                    <LinkItem link="https://space.bilibili.com/11037907" title="Bilibili" />
                    <LinkItem link="https://weibo.com/u/7026785047" title="Weibo" />
                </ul>
            </div>
        </BaseDialog>
    )
}

interface LinkItemProps {
    title: string
    link: string
}

function LinkItem(props: LinkItemProps) {
    const containerRef = React.useRef<HTMLLIElement>(null)

    useEffect(() => {
        const rootE = containerRef.current as HTMLElement;
        const liE = rootE.querySelector(".mdc-deprecated-list-item") as HTMLElement
        setupListItemRipple(liE)
    }, [])

    return (
        <li ref={containerRef}>
            {/*  dialog  */}
            <a className="mdc-deprecated-list-item mdc-deprecated-list-item__darken" href={props.link} target="_blank" tabIndex={-1}>
                <span className="mdc-deprecated-list-item__text link-item">{props.title}</span>
            </a>
            <hr className="mdc-deprecated-list-divider" />
        </li>
    )
}

let openCount = 0
export function showAboutMeDialog() {
    showDialog(<AboutMeDialog openCount={openCount++} />, ABOUT_DIALOG_WRAPPER_ID)
}
